

document.addEventListener('DOMContentLoaded',function(){
    IniciarApp();
})

function IniciarApp(){
    navegacionFija();
    CrearGaleria();
    ScrollNav();
    EaterEgg();
}

function EaterEgg(){
    link = document.querySelector('footer a');
    console.log("Hay un link",link);
    link.addEventListener('click', function(e){
        e.preventDefault;
        alert("Mensaje Reservado Para Eren; \n ¡Ten un Buen Día, te mando un fuerte abrazo!");
    })
   
};



function ScrollNav() {
    const enlaces = document.querySelectorAll('.contenido-header a');
    enlaces.forEach(enlace => {
        enlace.addEventListener('click', function (e) {
            e.preventDefault(); // Evitar que el enlace haga una navegación inmediata
            const sectionScroll = e.target.attributes.href.value;
            const section = document.querySelector(sectionScroll);     
            console.log(section);      
            section.scrollIntoView({ behavior: "smooth" });

        });
    });
}

function navegacionFija(){
    const nav = document.querySelector('header');
    const sobreFestival = document.querySelector('.sobre-festival')
    const body = document.querySelector('body')

    window.addEventListener('scroll', () =>{

        if (sobreFestival.getBoundingClientRect().top < 0) {
            nav.classList.add('fijo')
            body.classList.add('body-scroll')

        }else{
            nav.classList.remove('fijo')
            body.classList.remove('body-scroll')
        }
    })
}




function CrearGaleria(){
    const galeria = document.querySelector('.galeria-imagenes')
    for(let i = 1; i<= 12; i++)
    {
        const imagen = document.createElement('picture');
        imagen.innerHTML = `
            <source srcset="build/img/thumb/${i}.avif" type="image/avif">
            <source srcset="buil/img/thumb/${i}.webp" type="image/webp">
            <img loading="lazy" width="200" height="300" src="build/img/thumb/${i}.jpg" alt="imagen_vocalista">`;
            
            imagen.onclick = ()=>{
                mostrarImagen(i);
            }

           galeria.appendChild(imagen)
    }
    
    
}

function mostrarImagen(id){
    const imagen = document.createElement('picture');
        imagen.innerHTML = `
            <source srcset="build/img/grande/${id}.avif" type="image/avif">
            <source srcset="buil/img/grande/${id}.webp" type="image/webp">
            <img loading="lazy" width="200" height="300" src="build/img/grande/${id}.jpg" alt="imagen_galeria">`;

    
    const overlay = document.createElement('DIV');
    overlay.appendChild(imagen);
    overlay.classList.add('overlay')  
    overlay.onclick = ()=>{
        overlay.remove('overlay')
        body.classList.remove('fijar-body')
    };

    const CerarModal = document.createElement('P');
    CerarModal.textContent = 'X';
    CerarModal.classList.add('btn-cerrar');
    
    CerarModal.onclick = function(){
         overlay.remove();
         body.classList.remove('fijar-body')
    }
    overlay.appendChild(CerarModal);


    const body = document.querySelector('body');
    body.appendChild(overlay)
    body.classList.add('fijar-body')
}

    


